import React from "react";
import { Link } from "react-router-dom";
import logo from "../../../Resources/logo.png";
import "./header.styles.scss";

import Cart from "../Cart-Icon/cartIcon.component";

import { signOut } from "./../../../Firebase/auth";
import { MessageHandler } from "../../../UIHandler/uiHandler";

function Header({ authenticated }) {
  const handleCartLink = (e) => {
    e.preventDefault();
    MessageHandler.showMessage(
      "You need to be signed to go to Cart",
      "Sign in"
    );
  };

  return (
    <header>
      <nav className="header">
        <div className="header-container">
          <Link to="/" className="header-logo">
            <img src={logo} alt="" />
          </Link>
          <Cart authenticated={authenticated} />
          <div onClick={openBtn} id="menu-icon">
            <ion-icon name="menu-outline" size="large"></ion-icon>
          </div>
        </div>
      </nav>

      <ul id="side-nav" className="side-nav">
        <li onClick={openBtn}>
          <Link to="/Aurora_Store">
            <img src={logo} alt="logo" />
          </Link>
        </li>
        <li onClick={openBtn}>
          <Link to="/Aurora_Store/">Home</Link>
        </li>
        <li onClick={openBtn}>
          <Link to="/Aurora_Store/shop">Shop</Link>
        </li>
        <li onClick={openBtn}>
          {authenticated ? (
            <Link onClick={signOut} to="/Aurora_Store">
              Sign Out
            </Link>
          ) : (
            <Link to="/Aurora_Store/sign-in">Sign In</Link>
          )}
        </li>
        <li onClick={openBtn}>
          {authenticated ? (
            <Link to="/Aurora_Store/cart">
              <ion-icon size="large" name="cart-outline"></ion-icon>
            </Link>
          ) : (
            <a href="." onClick={handleCartLink}>
              <ion-icon size="large" name="cart-outline"></ion-icon>
            </a>
          )}
        </li>
      </ul>
    </header>
  );
}

const body = document.body;

function openBtn() {
  body.classList.toggle("open");
}

export default Header;
